<template>
  <div id="home-course">
    <head-top title="课程" :show-back="false">

      <r-dropdown ref="DropDown" class="head-right" slot="right">
        <van-icon ref="Scan" name="scan"></van-icon>
        <div slot="menu">
          <div class="head_menu_item" @click="scanOpen">扫一扫</div>
          <div class="head_menu_item" @click="codeOpen">邀请码</div>
        </div>
      </r-dropdown>
    </head-top>

    <van-tabs v-model="headTopActive" @change="changeCourse">
      <van-tab title="正在上课">
        <van-pull-refresh v-if="$store.getters.nowCourse" v-model="isLoading" @refresh="changeCourse"
                          class="pull_fresh">
          <course-list class="list_card" course-type="current" :course="[$store.getters.nowCourse]">
          </course-list>
        </van-pull-refresh>
        <div v-else class="list_remark">
          请点击右上角的
          <van-icon name="scan" :style="{fontSize:'18px',color:'#fff', background: '#00cbab'}"></van-icon>
          <div>选择 扫一扫 或 邀请码 加入课程</div>
        </div>
      </van-tab>
      <van-tab title="历史课程">
        <van-pull-refresh v-model="isLoading" :head-height="80" @refresh="changeCourse" class="pull_fresh">
          <van-list v-model="historyCourse.loading" :finished="historyCourse.finished" finished-text="没有更多了"
                    :error.sync="historyCourse.error" error-text="请求失败，点击重新加载" @load="historyStudentCourse">
            <course-list class="list_card" course-type="history" :course="historyCourse.data">
            </course-list>
          </van-list>
        </van-pull-refresh>
      </van-tab>
    </van-tabs>

    <!-- 输入邀请码加入班级 -->
    <van-popup v-model="invitedShow">
      <r-digit-input ref="digit" title="请输入邀请码" sub-title="请在下方输入邀请码加入班级" :code-length="6" btn-name="加入班级"
                     code-name="邀请码"
                     @submit="submitInvitedCode">
      </r-digit-input>
    </van-popup>

  </div>
</template>

<script>
import RDropdown from "@/components/r-dropdown";
import RDigitInput from "@/components/r-digit-input";
import courseList from "@/components/r-course-list";
import { FuncType, sendToAPP } from "@/utils/interFlow";
import { getNowDate, getSetDate } from "@/utils/index";

export default {
  name: "homeCourse",
  components: { RDropdown, RDigitInput, courseList },
  props: {},
  data () {
    return {
      headTopActive: 0,
      nowDate: getNowDate(),
      currentCourse: {
        form: {
          startDate: getSetDate("date", -7),
          endDate: getNowDate(),
          stuid: this.$store.getters.studentID || "",
          psize: 1,
          pno: 0,
          estate: 1
        },

        total: 0,
        loading: false,
        error: false,
        finished: false,
        data: []
      },
      historyCourse: {
        form: {
          key: "",
          startDate: getSetDate("date", -365),
          endDate: getNowDate(),
          stuid: this.$store.getters.studentID || "",
          psize: 10,
          pno: 0,
          estate: 2
        },

        total: 0,
        loading: false,
        error: false,
        finished: false,
        data: []
      },
      isLoading: false,
      invitedShow: false,
      inviteCode: "",
      classRoomID: ""
    };
  },
  beforeRouteEnter (to, from, next) {
    next(vm => {
      // 通过 `vm` 访问组件实例
      if (from.path.indexOf("/history") > -1) {
        vm.headTopActive = 1;
      }

      vm.$store.dispatch("ValidateCourse");
    });
  },
  created () {
    window.acceptQRcode = code => {
      console.log("接收扫码：", code);
      let codeJson = this.isJsonString(code);
      // this.$toast("接收扫码：" + JSON.stringify(code));
      this.joinClass(
        codeJson && codeJson["CIID"] ? codeJson["CIID"] : codeJson,
        "scan"
      );
      return true;
    };
  },
  mounted () {
    this.$nextTick(() => {
      // this.changeCourse();
      if (!this.$store.getters.localIP) {
        sendToAPP(FuncType.initLocalIP, "").then(res => {
          if (res) this.$store.commit("SET_IP", res);
        });
      }
    });
  },
  methods: {
    isJsonString (str) {
      try {
        if (typeof JSON.parse(str) === "object") {
          return JSON.parse(str);
        }
      } catch (e) {
      }
      return str;
    },
    // 开启扫一扫
    scanOpen () {
      this.$refs["DropDown"].hide();
      sendToAPP(FuncType.openQRcode, "");
    },
    // 打开输入邀请码
    codeOpen () {
      this.$refs["DropDown"].hide();
      this.invitedShow = true;
      setTimeout(() => {
        this.$refs["digit"].init();
      }, 0);
    },

    submitInvitedCode (arr) {
      this.joinClass(arr.join(""), "code");
    },
    /**
     * 一体化平台  加入班级
     */
    joinClass (codeValue, way) {
      this.inviteCode = way === "code" ? codeValue : "";
      let reqData = {
        CIID: way === "scan" ? codeValue : "",
        studentID: this.$store.getters.studentID,
        studentName: this.$store.getters.student["StudentName"],
        studentXH: this.$store.getters.userName,
        inviteCode: this.inviteCode
      };

      this.$store.dispatch("JoinClassByUser", reqData).then(res => {
        if (res.Result) {
          this.classRoomID = res["ClassroomID"];
          this.courseInfoGet();
        } else if (res.Info) {
          this.$toast(res.Info);
        } else {
          this.$toast("邀请码不正确");
        }
      }).catch(err => {
        this.$toast("操作失败");
      });
    },
    courseInfoGet () {
      this.$mainHttp.classInfo.dataInfo({
        ciID: this.$store.getters.courseID || ""
      }).then(res => {
        if (res.Result && res.Datas && res.Datas.length > 0) {
          let item = res.Datas[0];
          let courseInfo = {
            CHID: item.Key["CHID"],
            CIID: item.Key["CIID"],
            ClassID: item.Key["ClassID"],
            ClassroomID: item.Key["ClassroomID"],
            ClassroomName: item["ClsRoomName"],
            TeacherID: item.Key["TeacherID"],
            TeacherName: item["TeacherName"],
            ClassName: item["ClassName"],
            CHName: item["CHName"],
            CourseName: item["CourseName"],
            ClassDate: item.Key["ClassDate"],
            CreateTime: item.Key["CreateTime"]
          };
          this.$store.commit("SET_COURSE", courseInfo);
          this.$store.commit("SET_COURSE_ID", item.Key["CIID"]);
          // 更新正在上课的课程
          this.$store.commit("SET_NOW_COURSE", courseInfo);
          this.classRoomGet();
        } else if (res.Info) {
          this.$toast(res.Info);
        } else {
          this.$toast("获取课程信息失败");
        }
      }).catch(() => {
        this.$toast("操作失败");
      });
    },
    /**
     * 一体化平台  获取教室信息
     */
    classRoomGet () {
      this.$store.dispatch(
        "GetCourseServiceByUser",
        this.classRoomID
      ).then(res => {
        if (res.Result) {

        } else if (res.Info) {
          this.$toast(res.Info);
          // } else {
          //   this.$toast("获取教室服务失败");
        }
        this.linkClassRoom();
      }).catch(() => {
        // this.$toast("获取教室服务出错");
        this.linkClassRoom();
      });
    },
    /**
     * 老师平台  接入课程
     */
    linkClassRoom () {
      let reqData = {
        studentName: this.$store.getters.student["StudentName"],
        studentXH: this.$store.getters.userName,
        // classID: this.$store.getters.classID,
        studentID: this.$store.getters.studentID,
        // csID: this.$store.getters.classStudentID
        CIID: this.$store.getters.courseID,
        inviteCode: this.inviteCode
      };

      this.$mainHttp.class.joinClass(reqData).then(res => {
        if (res.Result) {
          this.$router.push("/course");
        } else if (res.Info) {
          this.$toast(res.Info);
        } else {
          this.$toast("加入班级失败");
          // 更新正在上课的课程
          this.$store.commit("SET_NOW_COURSE", null);
        }
      }).catch(() => {
        // 更新正在上课的课程
        this.$store.commit("SET_NOW_COURSE", null);
        this.$toast("操作失败");
      });
    },

    currentStudentCourse () {
      let item = this.currentCourse;
      this.isLoading = true;
      item.loading = true;
      if (item.form.pno === 0 || item.total > item.form.pno * item.form.psize) {
        item.form.pno++;
      } else {
        this.isLoading = false;
        item.loading = false;
        item.finished = true;
        return;
      }
      this.$mainHttp.classInfo
        .dataList(this.currentCourse.form)
        .then(res => {
          if (res.Result && res.Datas && res.Datas.length > 0) {
            res.Datas.forEach(item => {
              this.currentCourse.data.push(item);
            });
          }

          if (res.RowsCount) item.total = res.RowsCount;
          // 防止一间教室多个课程未下课，学生无法正常上课
          if (item.total > 1) item.total = 1;
          if (item.total <= item.form.pno * item.form.psize)
            item.finished = true;
          item.loading = false;
          this.isLoading = false;
        })
        .catch(err => {
          item.error = true;
          item.loading = false;
          this.isLoading = false;
        });
    },
    historyStudentCourse () {
      let item = this.historyCourse;
      this.isLoading = true;
      item.loading = true;
      if (item.form.pno === 0 || item.total > item.form.pno * item.form.psize) {
        item.form.pno++;
      } else {
        this.isLoading = false;
        item.loading = false;
        item.finished = true;
        return;
      }
      this.$mainHttp.classInfo
        .dataList(this.historyCourse.form)
        .then(res => {
          if (res.Result && res.Datas && res.Datas.length > 0) {
            res.Datas.forEach(item => {
              let sub = {
                CHID: item.Key["CHID"],
                CIID: item.Key["CIID"],
                ClassID: item.Key["ClassID"],
                ClassroomID: item.Key["ClassroomID"],
                ClassroomName: item["ClsRoomName"],
                TeacherID: item.Key["TeacherID"],
                TeacherName: item["TeacherName"],
                ClassName: item["ClassName"],
                CHName: item["CHName"],
                CourseName: item["CourseName"],
                ClassDate: item.Key["ClassDate"],
                CreateTime: item.Key["CreateTime"]
              };

              if (
                !this.historyCourse.data.find(
                  r => JSON.stringify(r) === JSON.stringify(sub)
                )
              )
                this.historyCourse.data.push(sub);
            });
          }

          if (res.RowsCount) item.total = res.RowsCount;
          if (item.total <= item.form.pno * item.form.psize) {
            item.finished = true;
          }
          item.loading = false;
          this.isLoading = false;
        })
        .catch(err => {
          item.error = true;
          item.loading = false;
          this.isLoading = false;
        });
    },
    changeCourse () {
      if (this.headTopActive === 0) {
        this.currentCourse.form.pno = 0;
        this.currentCourse.total = 0;
        this.currentCourse.error = false;
        this.currentCourse.finished = false;
        this.currentCourse.data = [];

        // this.currentStudentCourse();
        let item = this.currentCourse;
        item.finished = true;
        item.loading = false;
        this.isLoading = false;
      } else {
        this.historyCourse.form.pno = 0;
        this.historyCourse.total = 0;
        this.historyCourse.error = false;
        this.historyCourse.finished = false;
        this.historyCourse.data = [];
        this.historyStudentCourse();
      }
    }
  }
};
</script>

<style lang='less'>
@import url("./../../styles/common/base.less");

#home-course {
  .head-right {
    .head_menu_item {
      color: #323233;
      background-color: #fff;
      border: 1px solid #ebedf0;
      height: 40px;
      font-size: @font-info;
      line-height: 40px;
      padding: 0 15px;
      text-align: center;

      &:focus,
      &:hover {
        color: #00cbab;
        background: #ecf5ff;
      }
    }
  }

  .van-tabs__wrap {
    position: fixed;
    width: 100%;
    top: 43px;
    z-index: 9;
    background: #ebf0ef;
    height: 59px;

    .van-tabs__nav--line {
      height: 44px;
      background: none;
    }

    .van-tab {
      background: #fff;

      span {
        color: rgba(0, 203, 171, 1);
      }
    }

    .van-tabs__line {
      width: 16vw;
      height: 1px;
      background: rgba(0, 203, 171, 1);
    }
  }

  .van-tabs__content {
    padding-top: 59px;
  }

  .pull_fresh {
    height: calc(100vh - 44px - 59px);
    overflow-y: auto;

    .van-pull-refresh__track {
      margin-bottom: 50px;
      // min-height: calc(100% + 1px);
      // background-color: #ebf0ef;
    }
  }

  .course_date {
    width: calc(45vw);
    margin: 0 calc(1vw);
    margin-top: 10px;
    display: inline-flex;
  }

  .list_card {
    background: #fff;
  }

  .list_remark {
    padding-top: 50px;
    font-size: @font-sub-title;
    line-height: 2;
  }
}
</style>



